.board-container {
  font-family:"Comic Sans MS", "Comic Sans", cursive;
  margin: 10px;
  font-size: 23px;
  font-weight: bold;

}
.header {
  font-family:"Comic Sans MS", "Comic Sans", cursive;
  margin: 0px ;
  font-size: 36px;
  font-weight: bold;
  padding: 0;
  height: 90px;
  color: black;
  text-shadow: 3px 3px rgb(82,120,16);
}
.board-row {
  display: flex;
  align-items: center;
  justify-content: center;
}
.square{
  background-color: aquamarine;
  font-family:"Comic Sans MS", "Comic Sans", cursive;
  font-size: 23px;
  color: red;
  border-radius: 9px;
  margin: 1px;
}
.square:hover{
  background-color: rgb(18, 237, 164);
 
}
